<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">订单详情</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div>
            <p class="time">{{obj.orderCode}}</p>
            <div class="detail">
              <div class="order detail-item">
                <p><label>订单号：</label><span>{{obj.orderCode}}</span></p>
                <p><label></label><span class="red">{{obj.status|formatOrderStatus}}</span></p>
              </div>
              <div class="price detail-item">
                <p><label>品种：</label><span class="red">{{obj.kindCount}}</span></p>
                <p><label>总价：</label><span class="red bold">{{obj.totalMoney|formatMoeny}}</span><label>元</label></p>
              </div>
              <!-- <div class="detail-item">
                <label>交货日期：</label>
                <span>2018-03-12 10:16:30</span>
              </div> -->
              <div class="detail-item">
                <label>确认日期：</label>
                <span>{{obj.confirmDate|formatTime}}</span>
              </div>
              <div class="detail-item">
                <label>结算日期：</label>
                <span>{{obj.settlementDate|formatTime}}</span>
              </div>
            </div>
            <ul class="list">
              <li class="item" v-for="(item,index) in obj.entries">
                  <div class="left">
                    <img :src="item.picUrl" />
                  </div>
                  <div class="right">
                    <p class="name">{{item.name}}</p>
                    <div class="desc">
                      <p><label>规格：</label><span>{{item.specs}}</span></p>
                      <p><label>数量：</label><span>{{item.quantity}}</span><label>{{item.unit}}</label></p>
                    </div>
                    <p class="price"><label>￥</label><span>{{item.price|formatMoney}}</span></p>
                  </div>
                </li>
            </ul>
          </div>
        </scroll>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {getMerchantBillDetail} from 'api/features'
  import {getLastUrl} from 'common/js/util'
  import {ERR_CODE} from 'api/config'

  export default {
    data () {
      return {
        query: '',
        obj: {}
      }
    },
    created () {
      this._getMerchantBillDetail()
    },
    filters: {
      formatTime(val){
        if(!val){
          return ''
        }
        return val.substring(0,10) + ' ' + val.substring(11,19)
      }
    },
    methods: {
      back() {
        this.$router.back()
      },
      onQueryChange(newQuery) {
        this.query = newQuery
        //dosomething
      },
      _getMerchantBillDetail() {
        this.id = getLastUrl()
        if(!this.id){
          return
        }
        getMerchantBillDetail(this.id).then((res) => {
          if(res.errCode === ERR_CODE){
            this.obj = res.data
          }
        })
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    background $color-background
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        width 100%
        height 100%
        overflow hidden
        .time
          display flex
          height 30px
          align-items center
          color #999
          background #f5f5f5
          font-size $font-size-small-x
          margin-left 15px
          margin-right 15px
        .detail
          height 170px
          background #fff
          box-sizing border-box
          padding-left 15px
          padding-right 15px
          display flex
          flex-direction column
          .detail-item
            height 28px
            display flex
            align-items center
            color #666
            font-size $font-size-medium-m
            .red
              color #eb4747
            .bold
              font-size $font-size-medium-x
              font-weight bold
          .order
            margin-top 10px
            justify-content space-between
          .price
            justify-content space-between
        .list
          background #fff
          width 100%
          margin-top 10px
          .item
            height 110px
            display flex
            .left
              width 94px
              height 100%
              display flex
              align-items center
              justify-content center
              img
                display block
                height 70px
                width 70px
            .right
              display flex
              flex 1
              flex-direction column
              margin-right 15px
              .name
                font-size $font-size-medium-x
                color #4c4c4c
                margin-top 20px
                font-weight bold
              .desc
                display flex
                justify-content space-between
                height 36px
                align-items center
                color #4c4c4c
                font-size $font-size-small-x
              .price
                display flex
                justify-content flex-end
                color #eb4747
                label
                  font-size $font-size-small-x
                  display flex
                  align-items flex-end
                span
                  font-weight bold
                  font-size $font-size-medium-x

</style>



